.CheckoutSummary {
  text-align: center;
  width: 80%;
  margin: auto;
}

@media (min-width: 720px) {
  .CheckoutSummary {
    max-width: 600px;
  }
}

.CheckoutSummary h1 {
  color: var(--magenta);
  -webkit-text-stroke: 0.02em var(--magenta-lighter);
}

.CheckoutOptions {
  width: 80%;
  border: 1px solid #eee;
  -webkit-box-shadow: 0 2px 3px #ccc;
          box-shadow: 0 2px 3px #ccc;
  background: #fff;
  padding: 10px;
  margin: 10px auto;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}



