.Input {
  width: 100%;
  padding: 10px;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  position: relative;
}

.Label {
  font-weight: bold;
  display: block;
  margin-bottom: 8px;
}

.InputElement {
  display: block;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  width: 100%;
  outline: none;
  border: 1px solid #ddd;
  background-color: #fff;
  font: inherit;
  padding: 6px 10px;
}

.InputElement:focus {
  outline: none;
  background-color: #ddd;
}

.Invalid {
  border: 1px solid var(--magenta-light);
  background-color: var(--magenta-lighter);
}

.ValidationError {
  font-size: 0.8rem;
  color: var(--magenta);
  margin: 0;
  position: absolute;
}



